@import './colormap';

body {
	background-color: getColor('primary');
	color: getColor('title');

	font-family: 'Roboto', sans-serif;
	font-weight: 700;
	letter-spacing: 0.03em;

}

a,
address {
	color: inherit;
	font-style: normal;
	text-decoration: none;
}

ul {
	list-style-type: none;
}

h1,
h2,
h3,
h4,
p,
ul,
li {
	margin: 0;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

.logo {
	font-family: 'Raleway', sans-serif;
	font-size: 26px;
	line-height: 1.19;
	color: getColor('secondary');
}

.logo__header {
	color: getColor('logo-header');
}

.logo__footer {
	color: getColor('logo-footer');
}

.active {
	color: getColor('secondary');
}

.button {


	// display: flex;
	align-items: center;
	text-align: center;
	letter-spacing: 0.06em;

	box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08), 0px 2px 2px rgba(0, 0, 0, 0.12);
	border: none;
	border-radius: 4px;
}

.section__title {
	visibility: hidden;
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}